  color: #4a4a4a;
  font-family: $mainFont;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 18px 38px;
  border: 1px solid #eaeaea;
}
.orderform {
  display: flex;
  justify-content: space-between;
  margin-top: 63px;
}
.orderformAdress h2,
.orderformDiscount h2 {
  color: #222222;
  font-family: $mainFont;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.orderform input {
  width: 317px;
  padding: 16px 18px;
  color: #b1b1b1;
  font-family: $mainFont;
  font-size: 13px;
  font-weight: 300;
  border: 1px solid #eaeaea;
}
.orderformAdress h2 {
  margin-bottom: 22px;
}
.orderformAdress input[placeholder="Bangladesh"] {
  width: 286px;
  border-right: none;
}
.orderformAdress .orderformBtn {
  width: 31px;
  height: 50px;
  box-sizing: border-box;
  border-left: none;
}
.orderformAdress .orderformBtn i {
  color: #bdbdbd;
  font-size: 15px;
}
.orderformAdress input {
  margin-bottom: 20px;
}
.orderformAdress button {
  margin-top: 3px;
  width: 100px;
  height: 35px;
  color: #4a4a4a;
  font-family: $mainFont;
  font-size: 11px;
  font-weight: 900;
  text-transform: uppercase;
  padding: 2px 13px;
}
.orderformDiscount h2 {
  margin-bottom: 28px;
}
.orderformDiscount div {
  color: #000000;
  font-family: $mainFont;
  font-size: 14px;
  font-weight: 300;
  margin-bottom: 24px;
}
.orderformDiscount input {
  margin-bottom: 25px;
}
.orderformDiscount button {
  margin-top: 3px;
  width: 118px;
  height: 35px;
  color: #4a4a4a;
  font-family: $mainFont;
  font-size: 11px;
  font-weight: 900;
  text-transform: uppercase;
  padding: 2px 13px;
}
.orderformTotal {
  width: 360px;
  height: 214px;
  background-color: #f5f3f3;
  margin-top: 15px;
}
.orderformTotalSub {
  color: #4a4a4a;
  font-family: $mainFont;
  font-size: 11px;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 14px;
}
.orderformTotalSub span {
  margin-right: 20px;
}
.orderformTotalGrand {
  color: #f16d7f;
  font-family: $mainFont;
  font-size: 16px;
  font-weight: 900;
  text-transform: uppercase;
}
.orderformTotalGrand span {
  margin-right: 20px;
  color: #222222;
}
.orderformTotalСontainer {
  float: right;
  width: 275px;
  margin-bottom: 17px;
  padding-bottom: 22px;
  border-bottom: 1px solid #e2e2e2;
  text-align: right;
  margin-right: 41px;
  margin-top: 40px;
}
.orderformTotal button {
  margin-top: 3px;
  width: 273px;
  height: 50px;
  color: #ffffff;
  font-family: $mainFont;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 2px 13px;
  display: block;
  margin: 0 auto;
}
/* Checkout */
.checkout {
  width: 1140px;
  margin: 0 auto;
  margin-top: 90px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-content: center;
}
@media (max-width: 745px) {
  .checkout {
    width: auto;
    padding: 0 35px;
  }
}
.checkout details {
  padding-bottom: 20px;
  border-bottom: 1px solid #e8e8e8;
  padding-top: 20px;
  cursor: pointer;
}
.checkout details:first-child {
  padding-top: 0;
}
summary::-webkit-details-marker {
  display: none;
}
.checkout h2 {
  color: #222222;
  font-family: $mainFont;
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.checkoutForm {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: auto auto;
  padding-bottom: 40px;
}
@media (max-width: 745px) {
  .checkoutForm {
    grid-template-columns: 100%;
    grid-template-rows: auto;
  }
}
@media (max-width: 425px) {
  .checkoutForm {
    display: none;
  }
}
.checkout form {
  margin-left: 35px;
}
.checkoutFormHead {
  color: #222222;
  font-family: $mainFont;
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 39px;
  margin-bottom: 5px;
}
.checkoutFormDesc {
  color: #b3b2b2;
  font-family: $mainFont;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
}
#checkout,
#register {
  display: none;
}
label[for="checkout"],
label[for="register"] {
  display: block;
  padding-left: 20px;
  background: url(../images/checkout.png) no-repeat left center;
}
#checkout:checked + label,
#register:checked + label {
  background: url(../images/register.png) no-repeat left center;
}
.checkout label[for="checkout"] {
  margin-top: 34px;
}
.checkout label {
  color: #222222;
  font-family: $mainFont;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
}
.checkout ul {
  color: #b3b2b2;
  font-family: $mainFont;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  list-style-image: url(../images/arrows.png);
  padding: 0;
  margin-left: 15px;
  margin-top: 30px;
}
.checkoutFormBtn {
  padding: 19px 53px;
  margin-top: 30px;
}
.checkoutFormAccesses {
  margin-top: 27px;
  margin-bottom: 15px;
  color: #222222;
  font-family: $mainFont;
  font-size: 12px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
.checkoutFormAccesses:last-child {
  margin-top: 21px;
}
.checkoutFormAccesses span {
  height: 6px;
  color: #ff0d0d;
  font-family: $mainFont;
  font-size: 17px;
  font-weight: 400;
  line-height: 20px;
}
.checkout input[type="text"] {
  width: 388px;
  height: 41px;
  border: 1px solid #eaeaea;
  background-color: #ffffff;
}
.checkoutFormFootnote {
  color: #ff0d0d;
  font-family: $mainFont;
  font-size: 13px;
  font-weight: 400;
  line-height: 20px;
  margin-top: 11px;
  margin-bottom: -15px;
}
.checkoutFormForgot {
  color: #222222;
  font-family: $mainFont;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  margin-left: 32px;
}
/* Single*/
.singleBanner {
  height: 777px;
  background: url(../images/woman.png) no-repeat, #f7f7f7;
  background-size: 600px;
  background-position: 50% 50%;
  .singleBannerСontainer {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 1600px;
    .singleArrowLeft,
    .singleArrowRight {
      width: 47px;
      height: 47px;
      background-color: rgba(42, 42, 42, 0.1803921568627451);
      margin: 25% 0;
    }
    .singleArrowLeft {
      background: url(../images/singlearrowleft.png) no-repeat, rgba(42, 42, 42, 0.18);
      background-position: 50% 50%;
      cursor: pointer;
    }
    .singleArrowRight {
      background: url(../images/singlearrowright.png) no-repeat, rgba(42, 42, 42, 0.18);
      background-position: 50% 50%;
      cursor: pointer;
    }
  }
}
