/* Product */
.productWrap {
  margin-top: 59px;
  .productAccordion {
    .card {
      border: none;
      margin-bottom: 18px;
      border-radius: 0;
      button {
        color: #6f6e6e;
        font-family: $mainFont;
        font-size: 14px;
        font-weight: 700;
        line-height: 20px;
        text-transform: uppercase;
      }
      .card-body {
        color: #6f6e6e;
        font-family: $mainFont;
        font-size: 14px;
        font-weight: 400;
        line-height: 33px;
      }
    }
    .card-header {
      height: 36px;
      width: 240px;
      border-left: 5px solid #ef5b70;
      padding: 0;
      h5 {
        background: #fff;
        i {
          float: right;
          padding: 6px;
        }
      }
    }
    .card-header:first-child {
      border-radius: 0;
    }
  }
  .productSize {

    #inlineCheckbox1,
    #inlineCheckbox2,
    #inlineCheckbox3,
    #inlineCheckbox4,
    #inlineCheckbox5,
    #inlineCheckbox6,
    #inlineCheckbox7
     {
        display: none;
    }
    

    label[for="inlineCheckbox1"],
    label[for="inlineCheckbox2"],
    label[for="inlineCheckbox3"],
    label[for="inlineCheckbox4"],
    label[for="inlineCheckbox5"],
    label[for="inlineCheckbox6"],
    label[for="inlineCheckbox7"] {
        display: block;        
        padding-left: 20px;
        background: url(../images/check.png) no-repeat left center;
    }

    #inlineCheckbox1:checked + label,
    #inlineCheckbox2:checked + label,
    #inlineCheckbox3:checked + label,
    #inlineCheckbox4:checked + label,
    #inlineCheckbox5:checked + label,
    #inlineCheckbox6:checked + label,
    #inlineCheckbox7:checked + label {
     background: url(../images/checkin.png) no-repeat left center;
    }
    
    
    
    .checkout label[for="checkout"] {
        margin-top: 34px;
    }
    
    .checkout label {
        color: #222222;
        font-family: Lato, sans-serif;
        font-size: 12px;
        font-weight: 400;
        text-transform: uppercase;
    }

    .productSizeHead {
      color: #6f6e6e;
      font-family: $mainFont;
      font-size: 14px;
      font-weight: 700;
      line-height: 20px;
      text-transform: uppercase;
    }
    .productSizeVariables {
      color: #6f6e6e;
      font-family: $mainFont;
      font-size: 14px;
      font-weight: 500;
      line-height: 26px;
      width: 176px;
      display: block;
    }
    .productSizeSlash {
      border-right: 1px solid #dfdfdf;
      padding-right: 10px;
      padding-left: 10px;
    }
    .productSizeSlash:first-child {
      padding-right: 10px;
      padding-left: 0px;
    }
    .productSizeSlash:last-child {
      border-right: none;
      padding-right: 0px;
      padding-left: 10px;
    }
    label {
      color: #6f6e6e;
      font-family: "Open Sans", sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      text-transform: uppercase;
    }
    .form-check-inline {
      margin-right: 1.75rem;
    }
    .productSizePrice {
      color: #6f6e6e;
      font-family: "Open Sans", sans-serif;
      font-size: 14px;
      font-weight: 400;
      line-height: 26px;
      display: flex;
      justify-content: space-between;
    }
  }
  .productNavbar {
    margin-top: 42px;
  }
  .productSort {
    display: flex;
    padding-right: 10px;
    div {
      padding: 2px 11px;
      color: #6f6e6e;
      font-family: Lato;
      font-size: 14px;
      font-weight: 400;
      line-height: 26px;
      border: 1px solid #ebebeb;
      background-color: #ffffff;
      box-sizing: border-box;
      i {
        margin-left: 15px;
      }
    }
  }
  .feturedElements {
    .product {
      margin-top: 50px;
      padding-top: 15px;
      padding-bottom: 15px;
    }
    .feturedFotoProduct1 {
      background: url(../images/product1big.jpg) center center no-repeat;
    }
    .feturedFotoProduct2 {
      background: url(../images/product2big.jpg) center center no-repeat;
    }
    .feturedFotoProduct3 {
      background: url(../images/product3big.jpg) center center no-repeat;
    }
    .feturedFotoProduct4 {
      background: url(../images/product4big.jpg) center center no-repeat;
    }
    .feturedFotoProduct5 {
      background: url(../images/product5big.jpg) center center no-repeat;
    }
    .feturedFotoProduct6 {
      background: url(../images/product6big.jpg) center center no-repeat;
    }
    .feturedFotoProduct7 {
      background: url(../images/product7big.jpg) center center no-repeat;
    }
    .feturedFotoProduct8 {
      background: url(../images/product8big.jpg) center center no-repeat;
    }
    .feturedFotoProduct9 {
      background: url(../images/product9big.jpg) center center no-repeat;
    }
    .feturedFotoProduct1,
    .feturedFotoProduct2,
    .feturedFotoProduct3,
    .feturedFotoProduct4,
    .feturedFotoProduct5,
    .feturedFotoProduct6,
    .feturedFotoProduct7,
    .feturedFotoProduct8,
    .feturedFotoProduct9 {
      width: 263px;
      height: 277px;
    }
  }
  .productNavigation {
    .pagination {
      border-radius: 3px;
      border: 1px solid #ebebeb;
      background-color: #ffffff;
      display: flex;
      justify-content: space-between;
      width: 260px;
      padding: 9px;
    }
    .page-link {
      border: none;
      color: #c4c4c4;
      font-family: $mainFont;
      font-size: 16px;
      font-weight: bold;
      line-height: 26px;
      padding: 0;
    }
  }
  .productBtn {
    border-radius: 3px;
    border: 1px solid #ef5b70;
    background-color: #ffffff;
    float: right;
    padding: 9px 44px;
    color: #ef5b70;
    font-family: $mainFont;
    font-size: 16px;
    font-weight: bold;
    line-height: 26px;
  }
}
.productDelivery {
  margin-top: 95px;
  height: 341px;
  background-color: #222224;
  margin-bottom: -100px;
  .row {
    width: 1140px;
    margin: 0 auto;
